










































































import {Component, Vue, Watch} from 'vue-property-decorator';
import {GetAllCountriesDocument} from '../../../../gql';

@Component({
    layout: 'console',
    apollo: {
        GetAllCountries: {
            query: GetAllCountriesDocument
        }
    }
})
export default class CountryConfig extends Vue{
    private GetAllCountries
    private gridOptions: any = {};
    private gridApi: any = null;
    private defaultColDef = {
        sortable: true,
        editable: false,
        resizable: true,
        suppressMenu: true
    };
    private columnDefs = [
        {
            headerName: 'Name',
            filter: true,
            field: 'name'
        },
        {
            headerName: 'Core',
            filter: false,
            field: 'code'
        },
        {
            headerName: 'Active',
            filter: false,
            valueFormatter: (data) => data.data.enabled ? 'Active': 'Disabled'
        },
    ]

    mounted() {
    }

    onGridReady() {
        this.gridApi = this.gridOptions!.api;
        this.gridApi!.sizeColumnsToFit();
    }

    @Watch('GetAllCountries')
    onChangingCountry() {
        console.log(this.GetAllCountries)
    }
}
